import React from 'react'
import Addcat from "./Addcat.js"
import Sidebar from "../Sidebar/Sidebar.js"

const Mmenu = (props) => {
  console.log(props.tcat)

  const options = props.cats.map(cat => {
    return (
      <option key={cat} value={cat}>
        {cat}
      </option>
    );
  });



  return (
    <div className="mmenuwrapper"> {

  !props.ccatpanel?

      (<div className="addlinkbar">

      {/*Checking if the filter was fired*/}
        <select
          value={props.filteron ? props.filtercat : 'default'}
          name="cats"
          ref={ref => {
            this.catselect = ref;
          }}
          onChange={props.selectChange}
        >
          <option value="default">Homepage</option>
          {options}
        </select>
        <input id="links" type="text" onChange={props.urlInputChange} />
        <button
          onClick={() => {
            props.addLinkInfo();
          }}
        >
          AddLink
        </button>
        <button onClick={props.ccatPanelContr_open}>CreateCategory</button>



      </div>):(
        <Addcat
          close={props.close}
          handleAddCat={catname => props.handleAddCat(catname)}
        />
      )}
<div className="showside">
<Sidebar
  cat={props.tcat}
  reset={props.reset}
  catFilter={props.catFilter}
  mmenuContr = {props.mmenuContr}
  />

</div>
</div>
  )
}

export default Mmenu



